import React, { useState, useRef, useEffect } from 'react';
import Reboot from '../components/Reboot.js';
import { Chart, registerables } from 'chart.js';
import notify_Off from '../images/notify_Off.svg';
import notify_On from '../images/notify_On.svg';

Chart.register(...registerables);

const Sensor = (props) => {

  // Datasets
  let tempDataSet = [];
  let gyroXDataSet = [];
  let gyroYDataSet = [];
  let gyroZDataSet = [];
  let accXDataSet = [];
  let accYDataSet = [];
  let accZDataSet = [];

  // TS
  let motionPreTS;
  let motionAddTS;
  let tempAddTS;
  let tempPreTS;

  // Time
  let tempTime = [];
  let gyroTime = [];
  let accTime = [];

  // Temperature 
  const [switchTemp, setSwitchTemp] = useState(false); // If false, C is chosen and if true, F is chosen
  let tempMax; // -40C = -40F 
  let tempMin; // 125C = 257F
  let temps; // Array containing the temperature measurements
  let tempMeasurements; // Number of temperature measurements

  // Notifications
  let tempNotifyCharacteristic; // 001d0000-0001-11e1-ac36-0002a5d5c51b
  let motionNotifyCharacteristic; // 00e00000-0001-11e1-ac36-0002a5d5c51b
  let rebootCharacteristic;

  // Graph Bounds
  let minTempGraphBound;
  let maxTempGraphBound;
  let minGyroDataValue = 2000;
  let maxGyroDataValue = -2000;
  let minAccDataValue = 2000;
  let maxAccDataValue = -2000;
  const TEMP_GRAPH_MAX_LABELS = 50; // Maximum number of values displayed on the temperature chart
  const TEMP_GRAPH_BOUNDS_OFFSET = 0.5; // Offset added or subtracted from the maximum and minimum values of the temperature graph
  const MOTION_GRAPH_MAX_LABELS = 200; // Maximum number of values displayed on the motion charts
  const MOTION_GRAPH_BOUNDS_OFFSET = 100; // Offset added or subtracted from the maximum and minimum values of the motion graphs


  // Temperature chart
  // -----------------

  let tempChartConfig = {
    type: "line",
    data: {
      datasets: [{
        borderColor: '#00234B',
        backgroundColor: '#3CB4E6',
        data: tempDataSet,
        label: "Temperature",
      }],
    },
    options: {
      maintainAspectRatio: false,
      responsive: true,
      transition: {
        duration: 0,
      },
      plugins: {
        legend: {
          display: true
        }
      },
      scales: {
        x: {
          title: {
            display: true,
            text: 'Time (hh:mm:ss)',
            font: {
              size: 16,
            },
          },
          ticks: 
          {
            maxTicksLimit: 25,
          }
        },
        y: {
          title: {
            display: true,
            text: 'Temperature (C/F)',
            font: {
              size: 16,
            },
          },
          // Set the graph bounds
          min: minTempGraphBound,
          max: maxTempGraphBound,
        },
      },
    },
  };

  const tempChartContainer = useRef(null);
  const [tempChartInstance, setTempChartInstance] = useState(null);

  useEffect(() => {
    if (tempChartContainer && tempChartContainer.current) {
        const newTempChartInstance = new Chart(tempChartContainer.current, tempChartConfig);
        setTempChartInstance(newTempChartInstance);
    }
  }, [tempChartContainer]);

  const tempUpdateDataset = (datasetIndex, data) => {
    let currentTime = new Date();
    let time = currentTime.toLocaleTimeString("en-US", {
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit",
      hour12: false
    });

    if (tempDataSet.length >= TEMP_GRAPH_MAX_LABELS) {
      tempDataSet.pop(); // Remove the last temperature element
      tempDataSet.unshift(data); // Add data at the beginning of to the Array
      tempTime.pop(); // Remove the last time element
      tempTime.unshift(time); // Add current time at the beginning of to the Array
    } else {
      tempDataSet.unshift(data); // Add data at the beginning of to the Array
      tempTime.unshift(time); // Add current time at the beginning of to the Array
    }

    // Update the chart with new tempDataSet and tempTime values
    tempChartInstance.data.datasets[datasetIndex].data = tempDataSet;
    tempChartInstance.data.labels = tempTime;
    tempChartInstance.update();
  };

  // Gyroscope chart
  // ---------------

  let gyroChartConfig = {
    type: "line",
    data: {
      datasets: [{
        label: 'X',
        borderColor: '#00234B',
        backgroundColor: '#00234B',
        data: gyroXDataSet,
        pointRadius: 0, // Removes the point for each data
      },
      {
        label: 'Y',
        borderColor: '#3CB4E6',
        backgroundColor: '#3CB4E6',
        data: gyroYDataSet,
        pointRadius: 0,
      },
      {
        label: 'Z',
        borderColor: '#E6007E',
        backgroundColor: '#E6007E',
        data: gyroZDataSet,
        pointRadius: 0,
      }
    ],
    },
    options: {
      maintainAspectRatio: false,
      responsive: true,
      transition: {
        duration: 0,
      },
      plugins: {
        legend: {
          display: true
        }
      },
      scales: {
        x: {
          title: {
            display: true,
            text: 'Time (hh:mm:ss)',
            font: {
              size: 16,
            },
          },
          ticks: {
            maxTicksLimit: 25,
          }
        },
        y: {
          title: {
            display: true,
            text: 'Gyroscope (dps)',
            font: {
              size: 16,
            },
          },
          // Set the graph bounds
          min: minGyroDataValue,
          max: maxGyroDataValue,
        }
      }
    }
  }

  const gyroChartContainer = useRef(null);
  const [gyroChartInstance, setGyroChartInstance] = useState(null);

  useEffect(() => {
    if (gyroChartContainer && gyroChartContainer.current) {
        const newGyroChartInstance = new Chart(gyroChartContainer.current, gyroChartConfig);
        setGyroChartInstance(newGyroChartInstance);
    }
  }, [gyroChartContainer]);

  const gyroUpdateDataset = (datasetIndex, data) => {
    let currentTime = new Date();
    let time = currentTime.toLocaleTimeString("en-US", {
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit",
      hour12: false
    });
    if (datasetIndex === 0) {
      if (gyroXDataSet.length >= MOTION_GRAPH_MAX_LABELS) {
        gyroXDataSet.pop(); // Remove the last element
        gyroXDataSet.unshift(data); // Add data at the beginning of to the Array
      } else {
        gyroXDataSet.unshift(data); // Add data at the beginning of to the Array
      }
      gyroChartInstance.data.datasets[datasetIndex].data = gyroXDataSet;
    }
    if (datasetIndex === 1) {
      if (gyroXDataSet.length >= MOTION_GRAPH_MAX_LABELS) {
        gyroYDataSet.pop(); // Remove the last element
        gyroYDataSet.unshift(data); // Add data at the beginning of to the Array
      } else {
        gyroYDataSet.unshift(data); // Add data at the beginning of to the Array
      }
      gyroChartInstance.data.datasets[datasetIndex].data = gyroYDataSet;
    }
    if (datasetIndex === 2) {
      if (gyroXDataSet.length >= MOTION_GRAPH_MAX_LABELS) {
        gyroZDataSet.pop(); // Remove the last element
        gyroZDataSet.unshift(data); // Add data at the beginning of to the Array
        gyroTime.pop(); // Remove the last time element
      } else {
        gyroZDataSet.unshift(data); // Add data at the beginning of to the Array
      }
      gyroChartInstance.data.datasets[datasetIndex].data = gyroZDataSet;

      // Update the chart with new gyroDataSets and gyroTime values
      gyroTime.unshift(time);  // Add current time at the beginning of to the Array
      gyroChartInstance.data.labels = gyroTime;
      gyroChartInstance.update();
    }
  };

  // Accelerometer chart
  // -------------------

  let accChartConfig = {
    type: "line",
    data: {
      datasets: [{
        label: 'X',
        borderColor: '#00234B',
        backgroundColor: '#00234B',
        data: accXDataSet,
        pointRadius: 0, // Removes the point for each data
      },
      {
        label: 'Y',
        borderColor: '#3CB4E6',
        backgroundColor: '#3CB4E6',
        data: accYDataSet,
        pointRadius: 0,
      },
      {
        label: 'Z',
        borderColor: '#E6007E',
        backgroundColor: '#E6007E',
        data: accZDataSet,
        pointRadius: 0,
      }
    ],
    },
    options: {
      maintainAspectRatio: false,
      responsive: true,
      transition: {
        duration: 0,
      },
      plugins: {
        legend: {
          display: true
        }
      },
      scales: {
        x: {
          title: {
            display: true,
            text: 'Time (hh:mm:ss)',
            font: {
              size: 16,
            },
          },
          ticks: {
            maxTicksLimit: 25,
          },
        },
        y: {
          title: {
            display: true,
            text: 'Accelerometer (mg)',
            font: {
              size: 16,
            },
          },
          // Set the graph bounds
          min: minAccDataValue,
          max: maxAccDataValue,
        },
      },
    },
  }

  const accChartContainer = useRef(null);
  const [accChartInstance, setAccChartInstance] = useState(null);

  useEffect(() => {
    if (accChartContainer && accChartContainer.current) {
        const newAccChartInstance = new Chart(accChartContainer.current, accChartConfig);
        setAccChartInstance(newAccChartInstance);
    }
  }, [accChartContainer]);

  const accUpdateDataset = (datasetIndex, data) => {
    let currentTime = new Date();
    let time = currentTime.toLocaleTimeString("en-US", {
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit",
      hour12: false
    });
    if (datasetIndex === 0) {
      if (accXDataSet.length >= MOTION_GRAPH_MAX_LABELS) {
        accXDataSet.pop(); // Remove the last element
        accXDataSet.unshift(data); // Add data at the beginning of to the Array
      } else {
        accXDataSet.unshift(data); // Add data at the beginning of to the Array
      }
      accChartInstance.data.datasets[datasetIndex].data = accXDataSet;
    }
    if (datasetIndex === 1) {
      if (accXDataSet.length >= MOTION_GRAPH_MAX_LABELS) {
        accYDataSet.pop(); // Remove the last element
        accYDataSet.unshift(data); // Add data at the beginning of to the Array
      } else {
        accYDataSet.unshift(data); // Add data at the beginning of to the Array
      }
      accChartInstance.data.datasets[datasetIndex].data = accYDataSet;
    }
    if (datasetIndex === 2) {
      if (accXDataSet.length >= MOTION_GRAPH_MAX_LABELS) {
        accZDataSet.pop(); // Remove the last element
        accZDataSet.unshift(data); // Add data at the beginning of to the Array
        accTime.pop(); // Remove the last time element
      } else {
        accZDataSet.unshift(data); // Add data at the beginning of to the Array
      }
      accChartInstance.data.datasets[datasetIndex].data = accZDataSet;

      // Update the chart with new accDataSets and accTime values
      accTime.unshift(time); // Add current time at the beginning of to the Array
      accChartInstance.data.labels = accTime;
      accChartInstance.update();
    }
  };

  // Filtering the different datathroughput characteristics
  // ------------------------------------------------------

  props.allCharacteristics.map(element => {
    switch (element.characteristic.uuid) {
      case "001d0000-0001-11e1-ac36-0002a5d5c51b": // Temperature Notifications
        tempNotifyCharacteristic = element;
        break;
      case "00e00000-0001-11e1-ac36-0002a5d5c51b": // Motion Notifications
        motionNotifyCharacteristic = element;
      break;
      default:
        //console.log("# No characteristics found..");
    }
  });

  document.getElementById("readmeInfo").style.display = "none";

  // Enable notification button for temperature
  // ------------------------------------------

  async function onTempPanelButtonClick() {
    let notifyImg = document.getElementById('tempPanelButton'); // The image displayed (Notify On / Off)
    if (notifyImg.src.includes("Off")) { // If the image is Off and the image is clicked, notifications will be enabled
      console.log('Temperature notifications ON');
      console.log(tempNotifyCharacteristic.characteristic);
      tempNotifyCharacteristic.characteristic.startNotifications();
      tempNotifyCharacteristic.characteristic.oncharacteristicvaluechanged = tempNotifHandler;
      notifyImg.src = notify_On; // Changing the image displayed
      notifyImg.alt = "Temperature notifications ON";

      // Reset values for a new measurement
      tempDataSet = []; // Reset the chart everytime notifications are enabled
      tempTime = [];
      temps = [];

      // The STTS22H sensor used in the STM32WB5MM-DK board has an operating temperature range from -40C to 125C
      // Reset the min and maximum values to default
      tempMax = -40; // -40C = -40F 
      if (switchTemp) { // Fahrenheit
        tempMin = 257 // 125C = 257F
      } else { // Celcius
        tempMin = 125 
      }

      // Disable the slider if temperature notifications are enabled
      if (tempNotifyCharacteristic) {
        document.getElementById('slider').disabled = true;
      }

    } else { // If the image is On and the image is clicked, notifications will be disabled
      console.log('Temperature notifications OFF');
      console.log(tempNotifyCharacteristic.characteristic);
      tempNotifyCharacteristic.characteristic.stopNotifications();
      notifyImg.src = notify_Off; // Changing the image displayed
      notifyImg.alt = "Temperature notifications OFF";

      // Enable the slider if temperature notifications are disabled
      if (tempNotifyCharacteristic) {
        document.getElementById('slider').disabled = false;
      }
    }
  }

  // If the slider to change unit (C/F) is clicked
  const handleSliderChange = (event) => {
    if (!switchTemp) { 
      setSwitchTemp(true);
      console.log("Changed temperature unit to Fahrenheit");
    } else {
      setSwitchTemp(false);
      console.log("Changed temperature unit to Celcius");
    }
  }

  // Enable notification button for gyroscope
  // ----------------------------------------

  async function onNotifyGyroButtonClick(motionChoice=1) { // If motion choice is 1 it will only affect gyroscope
    let accNotifyImg = document.getElementById('accPanelButton'); // The image displayed (Notify On / Off)
    let gyroNotifyImg = document.getElementById('gyroPanelButton');
    // Handle different cases : if gyroscope is enabled or not and if accelerometer is enabled or not when the gyroscope notify image is clicked
    if (gyroNotifyImg.src.includes("Off") && accNotifyImg.src.includes("Off")) { // If both images are Off and the gyroscope notify image is clicked, motion notifications will be enabled and it will display the gyroscope data
      console.log('Gyroscope notifications ON');
      console.log(motionNotifyCharacteristic.characteristic);
      motionNotifyCharacteristic.characteristic.startNotifications();
      motionNotifyCharacteristic.characteristic.oncharacteristicvaluechanged = function(event) {
        motionNotifHandler(event, motionChoice);
      };     
      gyroNotifyImg.src = notify_On; // Changing the image displayed
      gyroNotifyImg.alt = "Gyroscope notifications ON";

    } else if (gyroNotifyImg.src.includes("Off") && accNotifyImg.src.includes("On")) { // If the gyroscope notify image is Off, the accelerometer one is On and the gyroscope notify image is clicked, motion notifications are already enabled so it just adds the display of the gyroscope data
      motionChoice = 0;
      console.log('Gyroscope notifications ON');
      console.log(motionNotifyCharacteristic.characteristic);
      motionNotifyCharacteristic.characteristic.oncharacteristicvaluechanged = function(event) {
        motionNotifHandler(event, motionChoice);
      };
      gyroNotifyImg.src = notify_On; // Changing the image displayed
      gyroNotifyImg.alt = "Gyroscope notifications ON"; 

    } else if (gyroNotifyImg.src.includes("On") && accNotifyImg.src.includes("On")) { // If both images are On and the gyroscope notify image is clicked, motion notifications will stay enabled but the accelerometer data won't be displayed anymore
      motionChoice = 2;
      console.log('Gyroscope notifications OFF');
      motionNotifyCharacteristic.characteristic.oncharacteristicvaluechanged = function(event) {
        motionNotifHandler(event, motionChoice);
      };
      gyroNotifyImg.src = notify_Off; // Changing the image displayed
      gyroNotifyImg.alt = "Gyroscope notifications OFF";
  
    } else { // If the gyroscope notify image is On, the accelerometer one is Off and the gyroscope notify image is clicked, motion notifications will be disabled
      console.log('Gyroscope notifications OFF');
      console.log(motionNotifyCharacteristic.characteristic);
      motionNotifyCharacteristic.characteristic.stopNotifications();
      gyroNotifyImg.src = notify_Off; // Changing the image displayed
      gyroNotifyImg.alt = "Gyroscope notifications OFF";
    }
  }

  // Enable notification button for accelerometer
  // --------------------------------------------

  async function onNotifyAccButtonClick(motionChoice=2) { // If motion choice is 2 it will only affect accelerometer
    let gyroNotifyImg = document.getElementById('gyroPanelButton'); // The image displayed (Notify On / Off)
    let accNotifyImg = document.getElementById('accPanelButton'); 
    // Handle different cases : if accelerometer is enabled or not and if gyroscope is enabled or not when the accelerometer notify image is clicked
    if (accNotifyImg.src.includes("Off") && gyroNotifyImg.src.includes("Off")) { // If both images are Off and the accelerometer notify image is clicked, motion notifications will be enabled and it will display the accelerometer data
      console.log('Accelerometer notifications ON');
      console.log(motionNotifyCharacteristic.characteristic);
      motionNotifyCharacteristic.characteristic.startNotifications();
      motionNotifyCharacteristic.characteristic.oncharacteristicvaluechanged = function(event) {
        motionNotifHandler(event, motionChoice);
      };     
      accNotifyImg.src = notify_On; // Changing the image displayed
      accNotifyImg.alt = "Accelerometer notifications ON";

    } else if (accNotifyImg.src.includes("Off") && gyroNotifyImg.src.includes("On")) { // If the accelerometer notify image is Off, the gyroscope one is On and the accelerometer notify image is clicked, motion notifications are already enabled so it just adds the display of the accelerometer data
      motionChoice = 0;
      console.log('Accelerometer notifications ON');
      console.log(motionNotifyCharacteristic.characteristic);
      motionNotifyCharacteristic.characteristic.oncharacteristicvaluechanged = function(event) {
        motionNotifHandler(event, motionChoice);
      };     
      accNotifyImg.src = notify_On; // Changing the image displayed
      accNotifyImg.alt = "Accelerometer notifications ON";

    } else if (accNotifyImg.src.includes("On") && gyroNotifyImg.src.includes("On")) { // If both images are On and the accelerometer notify image is clicked, motion notifications will stay enabled but the accelerometer data won't be displayed anymore
      motionChoice = 1;
      console.log('Accelerometer notifications OFF');
      motionNotifyCharacteristic.characteristic.oncharacteristicvaluechanged = function(event) {
        motionNotifHandler(event, motionChoice);
      };     
      accNotifyImg.src = notify_Off; // Changing the image displayed
      accNotifyImg.alt = "Accelerometer notifications OFF";

    } else { // If the accelerometer notify image is On, the gyroscope one is Off and the accelerometer notify image is clicked, motion notifications will be disabled
      console.log('Accelerometer notifications OFF');
      console.log(motionNotifyCharacteristic.characteristic);
      motionNotifyCharacteristic.characteristic.stopNotifications();
      accNotifyImg.src = notify_Off; // Changing the image displayed
      accNotifyImg.alt = "Accelerometer notifications OFF";
    }
  }

  // Enable all notifications button
  // -------------------------------

  async function onAllNotifsButtonClick() {
    // Get the states of the different buttons / images
    let allNotifStatus = document.getElementById('allNotifsButton').innerHTML;
    let tempNotifyImg = document.getElementById('tempPanelButton');
    let gyroNotifyImg = document.getElementById('gyroPanelButton');
    let accNotifyImg = document.getElementById('accPanelButton');
    let motionChoice = 0; // If motion choice is 0 it will affect gyroscope and accelerometer
    
    //  Enable or disable notifications based on what is already enabled or disabled
    if (allNotifStatus === "Enable all notifications") {
      console.log('Enabled all notifications');
      document.getElementById('allNotifsButton').innerHTML = "Disable all notifications"
      if (tempNotifyImg.src.includes("Off")) {
        onTempPanelButtonClick();
      }
      if (gyroNotifyImg.src.includes("Off")) {
        onNotifyGyroButtonClick(motionChoice);
      }
      if (accNotifyImg.src.includes("Off")) {
        onNotifyAccButtonClick(motionChoice);
      }
    } else {
      console.log('Disabled all notifications');
      document.getElementById('allNotifsButton').innerHTML = "Enable all notifications"
      if (tempNotifyImg.src.includes("Off")) {
      } else {
        onTempPanelButtonClick();
      }
      if (gyroNotifyImg.src.includes("Off")) {
      } else {
        onNotifyGyroButtonClick(motionChoice);
      }
      if (accNotifyImg.src.includes("Off")) {
      } else {
        onNotifyAccButtonClick(motionChoice);
      }
    }
  }

  // Management of temperature notifications
  // ---------------------------------------

  let unit; // For displaying temperature unit
  if (switchTemp) {
    unit = " F";
  } else {
    unit = " C";
  }
  
  function tempNotifHandler(event) {
    let tempTS;
    var tempTSBuf = new Uint16Array(event.target.value.buffer);
    let temp;
    var tempBuf = new Int16Array(event.target.value.buffer);

    console.log("Temperature Notification Received");
    console.log(JSON.stringify(tempBuf)); 
    
    tempTS = tempTSBuf[0]; // TS informations are located in the 0th part of the notification
    temp = (tempBuf[4]) / 10 // Temperature informations are located in the 4th part of the notification


    if (switchTemp) { // If F is chosen, convert to Farenheit
      temp = ((temp * 9 / 5) + 32).toFixed(1);
    }
    temps.push(temp); // Add the measurement to the list
    if (temp > tempMax) { // Check if the actual temperature is the highest
      tempMax = temp;
    }
    if (temp < tempMin) { // Check if the actual temperature is the lowest
      tempMin = temp;
    }

    // Manage the TS number and add 65536 (the max value of the buffer) if the buffer is exceeded
    if (tempAddTS === undefined) {
      tempAddTS = 0;
    }
    if (tempPreTS === undefined) {
      tempPreTS = -1;
    }
    tempTS += tempAddTS;
    if (tempPreTS > tempTS) {
      tempAddTS += 65536;
      tempTS += tempAddTS;
    }
    tempPreTS = tempTS;

    tempMeasurements = temps.length;
    
    // Display the values measured
    console.log("The lowest measured temperature is : " + tempMin);
    console.log("The actual measured temperature is : " + temp);
    console.log("The highest measured temperature is : " + tempMax);
    console.log("The number of temperature measurements is : " + tempMeasurements);

    var min = document.getElementById("tempMin");
    min.innerText = tempMin + unit;
    var actual = document.getElementById("temp");
    actual.innerText = temp + unit;
    var max = document.getElementById("tempMax");
    max.innerText = tempMax + unit;

    document.getElementById('tempTS').innerHTML = "TS : " + tempTS;
    document.getElementById('tempMeasurements').innerHTML = "Temperature measurements : " + tempMeasurements;

    // Set minimal bounds to zoom out on the graph when values vary little 
    minTempGraphBound = parseFloat(tempMin) - TEMP_GRAPH_BOUNDS_OFFSET;
    maxTempGraphBound = parseFloat(tempMax) + TEMP_GRAPH_BOUNDS_OFFSET;
    tempChartInstance.options.scales.y.min = minTempGraphBound;
    tempChartInstance.options.scales.y.max = maxTempGraphBound;

    // Update dataset
    tempUpdateDataset(0, temp.toString());
  }

  // Management of motion notifications 
  // ---------------------------------------

  function motionNotifHandler(event, motionChoice) {
    let TS;
    var TSbuf = new Uint16Array(event.target.value.buffer);
    var motionBuf = new Int16Array(event.target.value.buffer);
    
    console.log("Motion Notification Received");
    console.log(JSON.stringify(motionBuf)); 

    TS = TSbuf[0];

    // Manage the TS number and add 65536 (the max value of the buffer) if the buffer is exceeded
    if (motionAddTS === undefined) {
      motionAddTS = 0;
    }
    if (motionPreTS === undefined) {
      motionPreTS = -1;
    }
    TS += motionAddTS;
    if (motionPreTS > TS) {
      motionAddTS += 65536;
      TS += motionAddTS;
    }
    motionPreTS = TS;
    
    // Interact with the gyroscope only if gyroscope notifications are enabled or all notifications are enabled
    if (motionChoice === 1 || motionChoice === 0) {
      let gyroX;
      let gyroY;
      let gyroZ;
      gyroX = motionBuf[4] / 10;
      gyroY = motionBuf[5] / 10;
      gyroZ = motionBuf[6] / 10;

      // Display the values measured
      console.log("Gyroscope -> TS : " + TS + " / x : " + gyroX + " / y : " + gyroY + " / z : " + gyroZ);

      document.getElementById('gyroTS').innerHTML = "TS : " + TS;
      document.getElementById('gyroX').innerHTML = "X : " + gyroX;
      document.getElementById('gyroY').innerHTML = "Y : " + gyroY;
      document.getElementById('gyroZ').innerHTML = "Z : " + gyroZ;

      // Get extreme values from the dataset
      let gyroMin = Math.min(gyroX, gyroY, gyroZ);
      let gyroMax = Math.max(gyroX, gyroY, gyroZ);
  
      if (gyroMin < minGyroDataValue) {
        minGyroDataValue = gyroMin;
      }
      if (gyroMax > maxGyroDataValue) {
        maxGyroDataValue = gyroMax;
      }

      // Set minimal bounds to zoom out on the graph when values vary little 
      gyroChartInstance.options.scales.y.min = minGyroDataValue - MOTION_GRAPH_BOUNDS_OFFSET;
      gyroChartInstance.options.scales.y.max = maxGyroDataValue + MOTION_GRAPH_BOUNDS_OFFSET;

      // Update datasets
      gyroUpdateDataset(0, gyroX.toString());
      gyroUpdateDataset(1, gyroY.toString());
      gyroUpdateDataset(2, gyroZ.toString());
    }

    // Interact with the accelerometer only if gyroscope notifications are enabled or all notifications are enabled
    if (motionChoice === 2 || motionChoice === 0) {
      let accX;
      let accY;
      let accZ;
      accX = motionBuf[1];
      accY = motionBuf[2];
      accZ = motionBuf[3];

      // Display the values measured
      console.log("Accelerometer -> TS : " + TS + " / x : " + accX + " / y : " + accY + " / z : " + accZ);

      document.getElementById('accTS').innerHTML = "TS : " + TS;
      document.getElementById('accX').innerHTML = "X : " + accX;
      document.getElementById('accY').innerHTML = "Y : " + accY;
      document.getElementById('accZ').innerHTML = "Z : " + accZ;

      // Get extreme values from the dataset
      let amin = Math.min(accX, accY, accZ);
      let amax = Math.max(accX, accY, accZ);
  
      if (amin < minAccDataValue) {
        minAccDataValue = amin;
      }
      if (amax > maxAccDataValue) {
        maxAccDataValue = amax;
      }

      // Set minimal bounds to zoom out on the graph when values vary little 
      accChartInstance.options.scales.y.min = minAccDataValue - MOTION_GRAPH_BOUNDS_OFFSET;
      accChartInstance.options.scales.y.max = maxAccDataValue + MOTION_GRAPH_BOUNDS_OFFSET;

      // Update datasets
      accUpdateDataset(0, accX.toString());
      accUpdateDataset(1, accY.toString());
      accUpdateDataset(2, accZ.toString());
    }
  }

  return (
    <div className="container-fluid">
      <div className="container">
        {rebootCharacteristic === undefined ? null : <Reboot rebootCharacteristic={rebootCharacteristic}></Reboot>}
        <div className='row justify-content-center mt-3'>
          <div className='d-grid col-xs-6 col-sm-6 col-md-4 col-lg-4 m-2' >
            <div className='d-flex flex-row'>
              <button className="defaultButton w-100" type="button" onClick={onAllNotifsButtonClick} id="allNotifsButton">Enable all notifications</button>
            </div>
          </div>
        </div>
        <div className="pannel">
          <div className='tempRcorners3'>
            <div className='pannelContainer pannelTempTitle'>
              <label class="switch" id='tempPanelSwitch'>
                <input type="checkbox" onChange={handleSliderChange} id='slider' checked={switchTemp}></input>
                <span class="slider"></span>
              </label>
              <span className='pannelTempInfo boldSubTitle pannelUnits'>C/F</span>
              <span className='pannelTempInfo boldTitle pannelTitle'>Temperature</span>
              <span className='pannelTempInfo boldSubTitle pannelNotify'>Notify</span>
              <img src={notify_Off} alt="Notify Off" onClick={onTempPanelButtonClick} className='pannelTempButton' id="tempPanelButton"></img>
            </div>
            <div className='pannelContainer'> 
              <span className='pannelTempInfo pannelSubTitle'> Min </span>
              <span className='pannelTempInfo pannelSubTitle'> Actual </span>
              <span className='pannelTempInfo pannelSubTitle'> Max </span>
            </div>
            <div className='pannelContainer'> 
              <span className='pannelTempInfo' id='tempMin'> </span>
              <span className='pannelTempInfo' id='temp'> </span>
              <span className='pannelTempInfo' id='tempMax'> </span>
            </div>
          </div>
        </div>
        <div class="card text-dark bg-light mb-3">
          <div class="card-header" >Temperature Chart</div>
          <div class="card-body-sensor">
          <p class="card-text-sensor" id="tempTS">TS :</p>
            <p class="card-text-sensor" id="tempMeasurements">Temperature measurements :</p>
          </div>
          <div style={{height: "400px", width: "100%"}}>  
            <canvas ref={tempChartContainer}></canvas>
          </div>
          <div class="card-footer">
            <small class="text-muted"></small>
          </div>
        </div>
        <br></br>
        <div className="pannel">
          <div className='motionRcorners3'>
            <div className='pannelContainer pannelMotionTitle'>
              <span className='pannelMotionInfo boldTitle pannelTitle'>Gyroscope</span>
              <span className='pannelMotionInfo boldSubTitle pannelNotify'>Notify</span>
              <img src={notify_Off} alt="Notify Off" onClick={() => onNotifyGyroButtonClick()} className='pannelMotionButton' id="gyroPanelButton"></img>
            </div>
          </div>
        </div>
        <div class="card text-dark bg-light mb-3">
          <div class="card-header" >Gyroscope Chart</div>
          <div class="card-body-sensor">
            <p class="card-text-sensor" id="gyroTS">TS :</p>
            <p class="card-text-sensor" id="gyroX">X :</p>
            <p class="card-text-sensor" id="gyroY">Y :</p>
            <p class="card-text-sensor" id="gyroZ">Z :</p>
          </div>
          <div style={{height: "400px", width: "100%"}}>  
              <canvas ref={gyroChartContainer}></canvas>
          </div>
          <div class="card-footer">
              <small class="text-muted"></small>
          </div>
        </div>
        <br></br>
        <div className="pannel">
          <div className='motionRcorners3'>
            <div className='pannelContainer pannelMotionTitle'>
              <span className='pannelMotionInfo boldTitle pannelTitle'>Accelerometer</span>
              <span className='pannelMotionInfo boldSubTitle pannelNotify'>Notify</span>
              <img src={notify_Off} alt="Notify Off" onClick={() => onNotifyAccButtonClick()} className='pannelMotionButton' id="accPanelButton"></img>
            </div>
          </div>
        </div>
        <div class="card text-dark bg-light mb-3">
          <div class="card-header" >Accelerometer Chart</div>
          <div class="card-body-sensor">
            <p class="card-text-sensor" id="accTS">TS :</p>
            <p class="card-text-sensor" id="accX">X :</p>
            <p class="card-text-sensor" id="accY">Y :</p>
            <p class="card-text-sensor" id="accZ">Z :</p>
          </div>
          <div style={{height: "400px", width: "100%"}}>  
              <canvas ref={accChartContainer}></canvas>
          </div>
          <div class="card-footer">
              <small class="text-muted"></small>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Sensor;